* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  max-width: 70rem;
  margin: 4rem auto;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-auto-rows: 300px;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
